body{
  /* Inserting background with responive sizing*/
  position: relative;
  background-image: url('../img/background.png');
  /* Background image positioning */
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.navbar-brand{
  /* Creating box behind branding */
  padding: 0.5%;
  background-color:grey;

  /* Creating shadow behind box and text */
  box-shadow: 4px 4px 1px 2px black;
  text-shadow: 3px 2.5px black;
}

/* footer styling */
footer{
  /* To make the footer stick to the bottom of the window */
  bottom: 0;

  /* Creating Copyright banner */
  width: 100%;
  height: 5%;
  border-top: solid lightgrey;
  border-width: 5px;
  background-color: grey;
  text-align: center;
}

/* Portfolio page: Project pics placement adjustment */
.row.justify-content-around{
  margin-right:2%
}

/* Create spacing between Jumbotron and header/footer when max-width > 768px */
.BR{
  display:static;
}

/* Positioning Icons in About Me */
#bioIcons{
  margin-left: 80.5%;
}

/* Image zoom on mouse hover */
.hoverZoom{
  border-style: solid;
  border-width: 1px;
  transition: transform .2s;
  height: 300px;
  width: 500px;
}

.hoverZoom:hover{
  transform: scale(1.1);
}

#overlay{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.5);
  z-index: 10;
  display: none;
}

.details{
  position: absolute;
  top: -1.4%; 
  height: 33%; 
  width: 90%;
  z-index: 11; 
  display: none
}

/* Remove wow animation when collapsed menu is active */
@media screen and (max-width: 991px){
  .nav-item{
      animation-name: none !important;
      visibility: visible;
  }
}

/* Media queries when max-width = 768px */
@media screen and (max-width: 768px){
  /* Turning off spacing */
  .BR{
      display:none;
  }
  /* Positioning Icons in About Me */
  #bioIcons{
      margin-left: 70%;
  }

  /* Disable zoom on mobile/small screen */
  .hoverZoom:hover{
      transform: none;
  }
}

/* Media queries when max-width = 400px */
@media screen and (max-width: 400px){
  /* Adjusting porfolio pics to look better*/
  #rowPort{
      margin-left: -6%;
  }

  /* Turning off spacing */
  .BR{
      display:none;
  }

  /* Positioning Icons in About Me */
  #bioIcons{
      margin-left: 47%;
  }

  /* Disable zoom on mobile/small screen */
  .hoverZoom:hover{
      transform: none;
  }

}